.cust-textarea {
    width: 795px;
    height: 189px;
    padding: 20px;
    font-family: 'Raleway', sans-serif;
    font-size: 18px;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: left;
    color: #202020;
    font-weight: 500;
    border: 1px solid #B8B8B880;
    margin-bottom: 30px;

    &:active {
        outline: none;
    }

    &:focus {
        outline: none;
    }


}

@media only screen and (max-width : 1270px) {
    .cust-textarea {
        width: 100%;
        height: 189px;
    }
}
